import {
  Container,
  Header,
  Section,
  Separator,
  List,
  Item,
  Bullet,
} from "./styled";
import bulletImg from "../../images/list_bullet.svg";
import skills from "./skills";

const Skillset = () => {
  console.log(skills);
  return (
    <Section>
      <Container>
        <Header>My skillset includes üõ†Ô∏è</Header>
        <Separator />
        <List>
          {skills.map((skill) => {
            return (
              <Item>
                <Bullet src={bulletImg} alt="" />
                {skill.valueOf()}
              </Item>
            );
          })}
        </List>
      </Container>
    </Section>
  );
};
export default Skillset;
